'use client';

import * as React from 'react';
import { AlertCircleIcon, EyeIcon, EyeOffIcon } from 'lucide-react';
import Link from 'next/link';
import { signIn } from 'next-auth/react';
import { useForm } from 'react-hook-form';
import { useRouter } from 'next/navigation';
import { z } from 'zod';
import { zodResolver } from '@hookform/resolvers/zod';
import { Button, buttonVariants } from '@axiom-ui/react/button';
import { Input } from '@axiom-ui/react/input';
import { Alert, AlertDescription } from '@axiom-ui/react/alert';
import { Label } from '@axiom-ui/react/label';
import { AuthCard } from '../../_components/auth-card';

const formSchema = z.object({
  email: z
    .string()
    .email('Digite um e-mail válido')
    .min(1, 'Campo obrigatório'),
  password: z.string().min(1, 'Campo obrigatório'),
});

type FormSchemaType = z.infer<typeof formSchema>;

export function LoginForm() {
  const [isLoading, setIsLoading] = React.useState(false);
  const [showPassword, setShowPassword] = React.useState(false);
  const [alertError, setAlertError] = React.useState('');
  const router = useRouter();

  const {
    formState: { errors },
    handleSubmit,
    register,
  } = useForm<FormSchemaType>({
    resolver: zodResolver(formSchema),
    defaultValues: {
      email: '',
      password: '',
    },
  });

  async function onSubmit(data: FormSchemaType) {
    setIsLoading(true);

    const result = await signIn('credentials', {
      email: data.email.toLowerCase(),
      password: data.password,
      callbackUrl: '/welcome',
      redirect: false,
    });

    setIsLoading(false);

    if (result?.status === 401) {
      setAlertError('Usuário ou senha inválidos');
    }

    if (result?.ok) {
      router.replace(result.url || '');
    }
  }

  function togglePassword(e: React.MouseEvent<HTMLButtonElement>) {
    e.preventDefault();
    setShowPassword(!showPassword);
  }

  return (
    <AuthCard>
      <div className="grid gap-10">
        <form className="grid gap-6" onSubmit={handleSubmit(onSubmit)}>
          {alertError ? (
            <Alert variant="destructive">
              <AlertDescription className="flex gap-4">
                <AlertCircleIcon />
                {alertError}
              </AlertDescription>
            </Alert>
          ) : null}

          <div className="grid gap-2">
            <Label htmlFor="email">E-mail</Label>
            <Input
              autoCapitalize="none"
              autoComplete="email"
              autoCorrect="off"
              id="email"
              type="email"
              {...register('email')}
            />
            {errors.email ? (
              <p className="px-1 text-feedback-alert">{errors.email.message}</p>
            ) : null}
          </div>

          <div className="grid gap-2">
            <Label htmlFor="password">Senha</Label>
            <Input
              autoCapitalize="none"
              autoComplete="password"
              autoCorrect="off"
              endIcon={
                <button
                  aria-label={showPassword ? 'Esconder senha' : 'Mostrar senha'}
                  onClick={togglePassword}
                  type="button"
                >
                  {showPassword ? <EyeIcon /> : <EyeOffIcon />}
                </button>
              }
              id="password"
              type={showPassword ? 'text' : 'password'}
              {...register('password')}
            />
            {errors.password ? (
              <p className="px-1 text-feedback-alert">
                {errors.password.message}
              </p>
            ) : null}
          </div>

          <Button disabled={isLoading} loading={isLoading} size="large">
            Entrar
          </Button>
        </form>

        <div className="flex justify-center">
          <Link
            className={buttonVariants({
              variant: 'transparent',
              size: 'large',
            })}
            href="/recuperar-senha"
          >
            Esqueci minha senha
          </Link>
        </div>

        <Button disabled={isLoading} size="large" variant="outline">
          Entrar com Google
        </Button>

        <Alert>
          <AlertDescription className="text-center">
            Você ainda não tem uma conta iClinic?{' '}
            <Link href="/cadastro">Teste o iClinic gratuitamente</Link>
          </AlertDescription>
        </Alert>
      </div>
    </AuthCard>
  );
}
